import { useEffect } from "react";
import prisma from "../prisma/db";

export default async function WorkshopDetail({
  params,
}: {
  params: { id: string };
}) {
  const { id } = params;
  const workshop = await prisma.workshop.findUnique({
    where: { id: parseInt(id, 10) },
    include: {
      Verein: true, // Verein-Daten mit einbeziehen
    },
  });

  useEffect(() => {
    if (workshop) {
      const instagramUrl =
        workshop.instagramPostUrl ||
        workshop.Verein?.instagramProfileUrl || // Fallback auf Instagram-Profil des Vereins
        "https://instagram.com"; // Alternativ-Link zu Instagram-Profil oder Fehlermeldung
      window.location.href = instagramUrl;
    } else {
      window.location.href = "https://instagram.com";
    }
  }, [workshop]);

  return null; // Keine Ansicht, nur Weiterleitung
}
